
const requiredField=(value)=>{
	if(value) return undefined;
	return '   !'
};

const lengthField=(value)=>{
	if(value.length<35) return undefined;
	return '    35 !';
};
const requireName=(value)=>{
	let regexp=/[a-zA-Z--]+/i;
	if(regexp.test(value)) return undefined;
	return '  !';
}
const requiredEmail=(value)=>{
	let regexp=/^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/i;
	if(regexp.test(value)) return undefined;
	// alert("  @");
	return "Email  !";
};

export  {requiredField,lengthField,requiredEmail,requireName}